import React, { useState, useEffect } from "react";
import socketIOClient from "socket.io-client";
import { useNiconico } from "react-niconico";
import Confetti from "react-confetti";

import "./App.css";
var socket;

socket = socketIOClient();

function drawStar(ctx) {
    const image = new Image();
    image.onload = () => {
        console.log("cargado");
        ctx.imageSmoothingEnabled = false;
        ctx.drawImage(image, 50, 50, 500, 500);
    };
    image.src =
        "https://cdn.sstatic.net/Sites/stackoverflow/company/img/logos/so/so-icon.svg?v=6e4af45f4d66";

    return ctx;
}

function App() {
    const [party, setParty] = useState(true);

    const [ref, emitText] = useNiconico({
        displayMillis: 10000,
        lineWidth: 3,
        //lineWidth: 10,
    });
    useEffect(() => {
        socket.on("connect", () => {
            console.log("Successfully connected!");
            console.log(socket.id);
        });
        socket.on("get_message", (msg) => {
            console.log("get_message", msg);
            if (msg.text) emitText(String(msg.text));
        });
        socket.on("get_gifts", (msg) => {
            console.log("get_gifts", msg);
            setParty(true);
        });

        emitText("Configurado");
    }, [emitText]);

    return (
        <div>
            <div className="App App-header" ref={ref}></div>
            <Confetti
                style={{ width: "100vw", height: "100vh" }}
                numberOfPieces={party ? 350 : 0}
                recycle={false}
                onConfettiComplete={(confetti) => {
                    setParty(false);
                    confetti.reset();
                }}
                //drawShape={drawStar}
            />
        </div>
    );
}

export default App;
